import registerWorker from 'promise-worker/register';
import TreeTraversalService from '@/services/tree-traversal-service';
import JSONfn from 'json-fn';
var treeTraversalService = new TreeTraversalService();
export default registerWorker(function (data) {
  var trees = data.trees;
  var topToBottom = data.topToBottom || false;
  var nodeEvaluators = (data.nodeEvaluators || []).map(function (e) {
    return JSONfn.parse(e);
  });
  var nodeEvaluatorsData = data.nodeEvaluatorsData;
  var traversedTrees = treeTraversalService.traverseAllTrees(trees, nodeEvaluators, nodeEvaluatorsData, topToBottom);
  return traversedTrees;
});